/*-------- 1.3 Main Menu --------*/
.main-menu {
    position: relative;
    z-index: 9;
    top: 20px;
    float: left;
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    -webkit-transition: all 0.3s ease-in-out;
    -ms-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}

.main-menu ul, .main-menu ul li {
    position: relative;
    margin: 0;
    padding: 0;
    width: max-content;
}

.main-menu ul a, .main-menu ul a li {
    position: relative;
    margin: 0;
    padding: 0;
    display: block;
}

/*First level styles */
.main-menu > ul > li > a {
    color: #fff;
    padding: 0 23px 10px 8px;
    font-size: 14px;
    font-size: 1.08rem;
    font-weight: 500;
}

.main-menu > ul > li.black > ul > li > a {
    color: #444;
}

.main-menu > ul > li:hover > a {
    color: #e54750;
}

header.sticky .main-menu > ul > li > a, header.header_in .main-menu > ul > li > a, header.black_nav .main-menu > ul > li > a {
    color: #3d3434;
}

header.sticky .main-menu > ul > li:hover > a, header.header_in .main-menu > ul > li:hover > a, header.black_nav .main-menu > ul > li:hover > a {
    opacity: 1;
    color: #e54750 !important;
}

#close_in, #header_menu {
    display: none;
}

/* All styles for screen size over 992px
================================================== */
@media only screen and (min-width: 992px) {
    .main-menu {
        transform: translateX(-46.5%);
        left: 50%;
        position: absolute;
        width: auto;

    }
    .scrolled .main-menu {
        margin-top: -1.6rem;
    }
    header.ar .main-menu {
        transform: translateX(-50.4%);

    }

    .main-menu a {
        white-space: nowrap;
    }

    .main-menu ul li {
        display: inline-block;
    }

    .main-menu ul li.submenu:hover > a:before,
    .main-menu ul li.submenu:hover > a:after {
        bottom: -10px;
        opacity: 0;
    }

    /* Submenu*/
    .main-menu ul ul {
        position: absolute;
        border-top: 2px solid #e54750;
        z-index: 1;
        visibility: hidden;
        left: 3px;
        top: 100%;
        margin: 0;
        display: block;
        padding: 0;
        background: #fff;
        min-width: 175px;
        -webkit-box-shadow: 0px 6px 12px 0px rgba(0, 0, 0, 0.175);
        -moz-box-shadow: 0px 6px 12px 0px rgba(0, 0, 0, 0.175);
        box-shadow: 0px 6px 12px 0px rgba(0, 0, 0, 0.175);
        transform: scale(0.4);
        transform-origin: 10% top;
        -webkit-transition: all 0.6s cubic-bezier(0.77, 0, 0.175, 1);
        -moz-transition: all 0.6s cubic-bezier(0.77, 0, 0.175, 1);
        transition: all 0.6s cubic-bezier(0.77, 0, 0.175, 1);
        -webkit-border-radius: 3px;
        -moz-border-radius: 3px;
        -ms-border-radius: 3px;
        border-radius: 3px;
        opacity: 0;

        transition: all .4s ease-in-out;
    }

    .main-menu ul ul:before {
        bottom: 100%;
        left: 12%;
        border: solid transparent;
        content: " ";
        height: 0;
        width: 0;
        position: absolute;
        pointer-events: none;
        border-bottom-color: #e54750;
        border-width: 7px;
        margin-left: -7px;
    }

    .main-menu ul li:hover > ul {
        padding: 0;
        visibility: visible;
        opacity: 1;
        -webkit-transform: scale(1);
        transform: scale(1);
        -webkit-transition-delay: 0.2s;
        transition-delay: 0.2s;
        -webkit-transition-duration: 0s, 0.2s, 0.2s;
        transition-duration: 0s, 0.2s, 0.2s;
    }

    .main-menu ul ul li {
        display: block;
        height: auto;
        padding: 0;
    }

    .dark .main-menu ul ul a li {
        color: #f6f6f6;

    }

    .main-menu ul ul a li {
        font-size: 13px;
        font-size: 0.8125rem;
        color: #444;
        display: block;
        padding: 12px 15px;
        -moz-transition: all 0.3s ease;
        -o-transition: all 0.3s ease;
        -webkit-transition: all 0.3s ease;
        -ms-transition: all 0.3s ease;
        transition: all 0.3s ease;
    }

    .main-menu ul ul a:hover {
        background: #eee;
    }

    .dark .main-menu ul ul a:hover {
        background: #3e3e3e;
    }

    .main-menu ul ul li:last-child a {
        border-bottom: none;
    }

    .main-menu ul ul li:first-child a:hover {
        -webkit-border-radius: 3px 3px 0 0;
        -moz-border-radius: 3px 3px 0 0;
        -ms-border-radius: 3px 3px 0 0;
        border-radius: 3px 3px 0 0;
    }

    .main-menu ul ul li:last-child a {
        border-bottom: none;
    }

    .main-menu ul ul li:last-child a:hover {
        -webkit-border-radius: 0 0 3px 3px;
        -moz-border-radius: 0 0 3px 3px;
        -ms-border-radius: 0 0 3px 3px;
        border-radius: 0 0 3px 3px;
    }

    .main-menu ul ul li:hover > a {
        color: #e54750;
        padding-left: 18px;
    }

    /* Submenu 3rd level */
    .main-menu ul ul li.third-level > a {
        font-weight: normal !important;
    }

    .main-menu ul ul li.third-level > a:hover {
        background-color: #f9f9f9;
        color: #e54750;
        padding-left: 18px;
        opacity: 1;

    }

    .main-menu ul ul li.third-level > a:after {
        font-family: 'ElegantIcons';
        content: "\35";
        float: right;
        font-size: 16px;
        font-size: 1rem;
        margin-top: -2px;
    }

    .main-menu ul ul ul {
        position: absolute;
        border-top: 0;
        z-index: 1;
        height: auto;
        left: 100%;
        top: 0;
        margin: 0;
        padding: 0;
        background: #fff;
        min-width: 190px;
        -webkit-box-shadow: 0px 6px 12px 0px rgba(0, 0, 0, 0.175);
        -moz-box-shadow: 0px 6px 12px 0px rgba(0, 0, 0, 0.175);
        box-shadow: 0px 6px 12px 0px rgba(0, 0, 0, 0.175);
    }

    /* Arrows top 3rd level*/
    .main-menu ul ul ul:before {
        border-width: 0;
        margin-left: 0;
    }
}

@media only screen and (max-width: 991px) {
    img.scrolled {
        height: 65px !important;
    }

    #header_menu {
        display: block;
        text-align: center;
        position: relative;
